.relation_content{
      width: 100%;
      display: grid;
      grid-template-columns: 60px 1fr;
      gap: .5rem
}

.relation_content img{
      width: 60px;
      height: 60px;
      border-radius: 50%;
}

.relation_data strong{
      color: var(--yellow-100);
      font-size: 1.6rem;
      text-transform: uppercase;
}

.relation_data a{
      margin-left: auto;
      padding: .3rem 1rem;
      background: var(--yellow-100);
      text-transform: uppercase;
      border-radius: 999px;
      color: var(--purple-300);
      margin-top: .5rem;
}

.relation_about{
      width: 100%;
      height: auto;
      max-height: 110px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--yellow-100);
}

.relation_about p{
      font-size: 1.2rem;
}