.modal_content{
      position: fixed;
      width: 100%;
      height: 100vh;
      min-height: 100%;
      top: 0;
      left: 0;
      background: linear-gradient(var(--alpha) 50%, var(--yellow-200) 50%);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 100;
      opacity: 0;
      visibility: hidden;
}

.modal_content.show{
      animation: show .3s ease-in-out forwards;
}

@keyframes show{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.close_modal{
      position: absolute;
      bottom: 2rem;
      right: 2rem;
      width: 40px;
      height: 40px;
      background: rgba(0,0,0,.4);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
}

.close_modal *{
      color: var(--light);
      font-size: 2.5rem;
}

.video_content{
      position: relative;
      z-index: 2;
}

.video_box{
      width: 600px;
      height: 349px;
      background: #222;
      position: relative;
      border-bottom: solid 1rem var(--yellow-200);
}

.video_box:before{
      width: 70%;
      height: 50px;
      background: #000;
      border-radius: 50%;
      left: 50%;
      bottom: -25px;
      filter: blur(25px);
      transform: translateX(-50%);
      z-index: -1;
      opacity: .6;
}

.video_box iframe{
      border: none;
}