.content{
      width: 750px;
      height: 760px;
      display: grid;
      grid-template-columns: 350px 400px;
      background: #000;
      box-shadow: 0 0 80px rgba(0,0,0,.4);
      position: relative;
}

.content:before,
.content:after{
      width: 470px;
      height: 470px;
      background: var(--purple-200);
      border-radius: 100px;
      z-index: -1;
}

.content:before{
      left: 0;
      top: 0;
      transform: translate(-120%, -80%) rotate(35deg);
}

.content:after{
      right: 0;
      bottom: 0;
      transform: translate(120%, 80%) rotate(-35deg);
}

.content_banner{
      height: 100%;
      position: relative;
}

.banner_img{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
}

.banner_img:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0,0,0,.3) 425px, #000 540px);
      top: 0;
      left: 0;
}

.wrapper_user{
      position: relative;
      padding: 2rem;
      display: flex;
      gap: .5rem;
}

.user_avatar{
      width: 60px;
      height: 60px;
      position: relative;
      border-radius: 50%;
      box-shadow: 0 0 0 .4rem var(--alpha);
      margin: .5rem;
}

.user_avatar img{
      width: 60px;
      height: 60px;
      border-radius: 50%;
}

.banner_img img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_avatar .dots{
      width: 10px;
      height: 10px;
      position: absolute;
      top: .5rem;
      right: 0;
      background: var(--light);
      border-radius: 50%;
      box-shadow: 0 0 0 .3rem var(--alpha);
}

.user_avatar .dots.green{
      background: var(--green);
}

.user_avatar .dots.red{
      background: var(--red);
}

.user_data{
      flex: 1;
}

.user_data h1{
      font-size: 2rem;
      color: var(--light);
}

.user_data p{
      color: var(--light);
      text-align: left;
      font-size: 1.2rem;
}

.online_box{
      padding: .5rem 1.5rem;
      background: var(--purple-200);
      border-radius: 999px;
      display: inline-flex;
}

.online_box span{
      color: var(--light);
      text-transform: uppercase;
}

.user_id{
      position: relative;
      transform: translateY(-2rem);
      background: var(--purple-200);
      border-radius: 0 0 999px 999px;
      padding: 2rem .5rem;
}

.user_id h2{
      writing-mode: vertical-rl;
      text-orientation: upright;
      color: var(--light);
      font-size: 1.2rem;
}

.content_banner__footer{
      width: 100%;
      height: auto;
      padding: 2rem;
      position: absolute;
      bottom: 0;
      left: 0;
      color: var(--light);
}

.social_menu{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: .6rem;
}

.description_user{
      margin-top: 5rem;
}

.description_user p{
      font-size: 1.2rem;
}

.location{
      margin-top: 2rem;
      display: flex;
      align-items: baseline;
      position: relative;
      z-index: 2;
}

.location_icon{
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
}

.location_icon:before{
      width: 10px;
      height: 10px;
      background: transparent;
      border-radius: 50%;
      animation: shadow_icon 1s ease-in-out infinite;
      z-index: -1;
}

@keyframes shadow_icon{
      0%{
            box-shadow: 0 0 0 0 var(--purple-100);
      }
      100%{
            box-shadow: 0 0 0 1.5rem transparent, 0 0 0 2.5rem transparent, 0 0 0 3rem transparent;
      }
}

.location_icon *{
      font-size: 2rem;
      color: var(--yellow-100);
}

.location span{
      font-size: 1.4rem;
}

.content_data{
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-rows: repeat(4, 190px);
      color: var(--light);
}

.data_row{
      width: 100%;
      height: 100%;
      position: relative;
      padding-left: 4.5rem;
      border-bottom-left-radius: 9.5rem;
}

.data_row.nav{
      background: var(--yellow-100);
      z-index: 4;
}

.data_row.video{
      background: var(--purple-300);
      box-shadow: 0 -100px var(--purple-300);
      z-index: 3;
}

.data_row.friend{
      background: var(--purple-200);
      box-shadow: 0 -100px var(--purple-200);
      z-index: 2;
}

.data_row.relation{
      background: var(--purple-100);
      box-shadow: 0 -100px var(--purple-100);
      z-index: 1;
}

.nav_title{
      text-align: right;
      text-transform: uppercase;
      color: var(--purple-200);
      padding: 1rem;
      font-size: 1.6rem;
}

.nav_menu{
      width: 322px;
      height: 82px;
      margin-top: 2rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.nav_menu a{
      width: 82px;
      height: 82px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      box-shadow: 0 0 0 2px rgba(0,0,0,.2) inset;
}

.nav_menu a *{
      color: var(--purple-200);
      font-size: 3.5rem;
}

.nav_menu a:before{
      content: attr(data-tooltip);
      text-transform: uppercase;
      color: var(--light);
      background: var(--purple-200);
      border-radius: .3rem;
      padding: .5rem;
      top: -5rem;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.nav_menu a:hover:before{
      top: -3rem;
      opacity: 1;
      visibility: visible;
}

.row_content{
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: 1fr 82px;
      gap: .5rem;
      padding: 1rem 1rem 2rem 0;
}

.row_content__right h2{
      text-transform: uppercase;
      width: 100%;
      text-align: right;
      color: var(--yellow-100);
      font-size: 1.6rem;
}

.row_icon{
      width: 82px;
      height: 82px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      box-shadow: 0 0 0 2px rgba(0,0,0,.2) inset;
      margin-top: 3rem;
}

.row_icon *{
      color: var(--yellow-100);
      font-size: 3.5rem;
}

.row_content_left.friend{
      height: 165px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--yellow-100);
}

.row_content_left.video{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
}

.row_content_left.video h3{
      text-align: center;
      font-size: 1.6rem;
      font-weight: 500;
}

.btn_video{
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: none;
      margin-top: 1rem;
}

.btn_video *{
      font-size: 5rem;
      color: var(--yellow-100);
}