@import url('https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Fira+Mono:wght@400;500;700&family=Poppins:wght@200;300;400;500;600&family=Xanh+Mono&display=swap');

:root{
  /** fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Courier Prime', monospace;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --dark: #080908;
  --light: #fff;
  --red: #d90000;
  --alpha: rgba(0,0,0,.38);

  /** default theme */
  --yellow-300: #e3a923;
  --yellow-200: #efb428;
  --yellow-100: #ffbf2a;
  --purple-300: #3a33a7;
  --purple-200: #4e36a9;
  --purple-100: #683aaf;

  /** globals */
  font-family: var(--ft-1);
  font-size: 62.5%;

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 920px;
  overflow: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, var(--yellow-300), var(--yellow-100));
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--alpha);
  border-radius: 20px;
}

::-webkit-scrollbar-thumb{
  background: var(--yellow-100);
  border-radius: 20px;
}