.friends_col{
      width: 98%;
      height: auto;
      display: flex;
      flex-direction: column;
      gap: .7rem;
}

.friend_row{
      width: 100%;
      height: 50px;
      display: grid;
      grid-template-columns: 50px 1fr;
      gap: .5rem;
}

.friend_row img{
      width: 50px;
      height: 50px;
      border-radius: 50%;
}

.friend_row_data{
      position: relative;
      height: 100%;
      overflow: hidden;
}

.friend_row_data a{
      position: absolute;
      top: 0;
      right: 0;
      color: var(--purple-300);
      text-transform: uppercase;
      padding: .3rem .75rem;
      background: var(--yellow-100);
      border-radius: 999px;
}

.friend_row_data strong{
      color: var(--yellow-100);
      font-size: 1.4rem;
}

.friend_row_data p{
      font-size: 1.2rem;
}