.btn_social{
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--purple-200) inset;
      transition: .2s;
      flex-shrink: 0;
}

.btn_social *{
      color: var(--light);
      font-size: 1.6rem;
}

.btn_social:hover *{
      color: var(--yellow-200);
}

.btn_social:hover{
      box-shadow: 0 0 0 .5rem var(--purple-200) inset;
}